import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

import verifyApplyInfo from '@/view/crowdTest/verify-apply-info.vue'; //确认申请信息


import moment from 'moment'
Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});


Vue.use(Router)

const router = new Router({
  // mode: 'history',
  // base: '/verify-apply-info.html',
  routes: [{
      path: '/',
      name: 'verifyApplyInfo',
      component: verifyApplyInfo
    },
  ]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



